<template>
  <div class="login-container">
    <div class="login-card">
      <div class="login-header">
        <div class="logo-section">
          <img src="/src/assets/images/avatar/profile.jpg" alt="Logo" class="logo" />
          <h1>管理后台</h1>
          <p>孙可馨的个人空间</p>
        </div>
      </div>
      
      <t-form
        ref="formRef"
        :data="formData"
        :rules="rules"
        @submit="handleSubmit"
        class="login-form"
      >
        <t-form-item name="username">
          <t-input
            v-model="formData.username"
            placeholder="用户名"
            size="large"
            prefix-icon="user"
          />
        </t-form-item>
        
        <t-form-item name="password">
          <t-input
            v-model="formData.password"
            type="password"
            placeholder="密码"
            size="large"
            prefix-icon="lock-on"
          />
        </t-form-item>
        
        <t-form-item>
          <t-button
            theme="primary"
            type="submit"
            size="large"
            :loading="isLoading"
            block
          >
            {{ isLoading ? '登录中...' : '登录' }}
          </t-button>
        </t-form-item>
      </t-form>
      
      <div class="login-footer">
        <p>默认账号：admin / admin123</p>
        <router-link to="/" class="back-link">
          <t-icon name="chevron-left" />
          返回网站首页
        </router-link>
      </div>
    </div>
    
    <div class="background-decoration">
      <div class="decoration-item decoration-1"></div>
      <div class="decoration-item decoration-2"></div>
      <div class="decoration-item decoration-3"></div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { MessagePlugin } from 'tdesign-vue-next'
import { useAuthStore } from '@/stores/auth'

const router = useRouter()
const authStore = useAuthStore()

const formRef = ref()
const isLoading = ref(false)

// 表单数据
const formData = reactive({
  username: '',
  password: ''
})

// 表单验证规则
const rules = {
  username: [
    { required: true, message: '请输入用户名', type: 'error' }
  ],
  password: [
    { required: true, message: '请输入密码', type: 'error' }
  ]
}

// 处理登录
const handleSubmit = async () => {
  const formResult = await formRef.value?.validate()
  if (!formResult) return
  
  isLoading.value = true
  
  try {
    const success = await authStore.login(formData.username, formData.password)
    
    if (success) {
      MessagePlugin.success('登录成功')
      router.push('/admin/dashboard')
    } else {
      MessagePlugin.error('用户名或密码错误')
    }
  } catch (error) {
    MessagePlugin.error('登录失败，请重试')
  } finally {
    isLoading.value = false
  }
}
</script>

<style lang="scss" scoped>
@import "@/styles/variables.scss";

.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, $primary-color, mix(black, $primary-color, 15%));
  padding: $spacing-lg;
  position: relative;
  overflow: hidden;
}

.login-card {
  background: rgba($white, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: $spacing-xl;
  width: 100%;
  max-width: 400px;
  box-shadow: $shadow-xl;
  z-index: 10;
}

.login-header {
  text-align: center;
  margin-bottom: $spacing-xl;
  
  .logo-section {
    .logo {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      margin-bottom: $spacing-md;
      box-shadow: $shadow-md;
    }
    
    h1 {
      margin: 0 0 $spacing-xs 0;
      color: $primary-color;
      font-size: 1.8rem;
      font-weight: 600;
    }
    
    p {
      margin: 0;
      color: $text-secondary;
      font-size: 0.9rem;
    }
  }
}

.login-form {
  .t-form-item {
    margin-bottom: $spacing-lg;
  }
}

.login-footer {
  text-align: center;
  margin-top: $spacing-xl;
  
  p {
    color: $text-secondary;
    font-size: 0.8rem;
    margin-bottom: $spacing-md;
  }
  
  .back-link {
    display: inline-flex;
    align-items: center;
    gap: $spacing-xs;
    color: $primary-color;
    text-decoration: none;
    font-size: 0.9rem;
    transition: color $transition-normal ease;
    
    &:hover {
      color: $accent-color;
    }
  }
}

.background-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  
  .decoration-item {
    position: absolute;
    background: rgba($white, 0.1);
    border-radius: 50%;
    animation: float 6s ease-in-out infinite;
    
    &.decoration-1 {
      width: 100px;
      height: 100px;
      top: 10%;
      left: 10%;
      animation-delay: 0s;
    }
    
    &.decoration-2 {
      width: 150px;
      height: 150px;
      top: 60%;
      right: 10%;
      animation-delay: 2s;
    }
    
    &.decoration-3 {
      width: 80px;
      height: 80px;
      bottom: 20%;
      left: 20%;
      animation-delay: 4s;
    }
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

@media (max-width: $breakpoint-md) {
  .login-container {
    padding: $spacing-md;
  }
  
  .login-card {
    padding: $spacing-lg;
  }
}
</style>